.order {
    display: flex;
    flex-direction: column;

    width: 100%;
    height: 80px;

    overflow-y: hidden;

    padding: 20px 30px;
    margin-bottom: 20px;

    border-radius: 5px;

    transition: all 500ms;

    box-shadow: 0px 0px 20px 0px lightgray;
    background-color: white;
}

.order .main-panel {
    display: flex;
    flex-direction: row;
    align-items: center;

    width: 100%;
}

.order .main-panel .name {
    color: #383838;
    font-size: 20px;
    font-weight: 700;

    margin-right: 40px;
}

.order .main-panel .date {
    color: rgba(0, 0, 0, 0.4);
    font-size: 16px;
    font-weight: 400;

    margin-right: 40px;
}

.order .main-panel .trigger {
    display: flex;
    flex-direction: row;
    align-items: center;

    margin-right: 20px;
}

.order .main-panel .trigger ion-icon {
    color: #A161B3;

    width: 16px;
    height: 16px;

    padding: 5px;
    margin-left: 5px;

    border-radius: 50%;

    transition: all 200ms;
}

.order .main-panel .trigger ion-icon:hover {
    background-color: rgba(0, 0, 0, 0.07);
}

.order .main-panel .payment {
    color: rgba(0, 0, 0, 0.4);
    font-size: 16px;
    font-weight: 400;

    margin-right: 40px;

    width: 120px;
}

.order .main-panel .price {
    color: #383838;
    font-size: 20px;
    font-weight: 700;
}

.order .main-panel .button-filled {
    margin-right: 20px;
}

.order .dropdown {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: flex-start;

    overflow-x: scroll;

    padding-top: 40px;
}

.order .dropdown::-webkit-scrollbar {
    display: none;
}

#products {
    width: 100%;

    display: flex;
    flex-direction: row;
}

#products .product {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 120px;

    padding: 20px;
}

#products .product .image {
    width: 100px;
    height: 100px;
    object-fit: contain;

    margin-bottom: 15px;
}

#products .product .name {
    color: #383838;
    font-size: 15px;
    font-weight: 700;
}

#products .product .amount {
    color: #383838;
    font-size: 14px;
    font-weight: 400;
}

#info {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

#info .left, #info .right {
    display: grid;
    grid-template-columns: auto auto;
    gap: 10px 50px;
}

#info .left {
    margin-right: 100px;
}

#info .heading {
    color: #383838;
    font-size: 16px;
    font-weight: 700;
}

#info .info {
    color: #383838;
    font-size: 16px;
    font-weight: 400;
}

